.register {
    height: 85vh;
    background-color: rgb(193, 190, 255);
    display: flex;
    align-items: center;
    justify-content: center;
  
    .card {
      width: 50%;
      display: flex;
      background-color: #fff;
      border-radius: 10px;
      min-height: 400px;
      overflow: hidden;
      flex-direction: row-reverse;
  
      .left {
        flex: 1;
        background: linear-gradient(rgba(39, 11, 96, 0.5), rgba(39, 11, 96, 0.5)),
        url("https://images.pexels.com/photos/4881619/pexels-photo-4881619.jpeg?auto=compress&cs=tinysrgb&w=1600")
            center;
        background-size: cover;
        padding: 20px;
        display: flex;
        flex-direction: column;
        gap: 20px;
        color: #fff;
  
        h1 {
          font-size: 90px;
          line-height: 90px;
        }
        p {
        }
        span {
          font-size: 14px;
        }
        button {
          width: 50%;
          border: none;
          padding: 14px;
          background-color: #fff;
          color: rebeccapurple;
          font-weight: bold;
          cursor: pointer;
        }
      }
  
      .right {
        flex: 1;
        padding: 50px;
        display: flex;
        flex-direction: column;
        gap: 50px;
        justify-content: center;
  
        h1 {
          color: #555;
        }
        form {
          display: flex;
          flex-direction: column;
          gap: 30px;
  
          input {
            border: none;
            border-bottom: 1px solid lightgray;
            padding: 20px 10px;
          }
          button {
            margin-top: 25px;
            width: 50%;
            padding: 14px;
            border: none;
            background-color: #938eef;
            font-weight: bold;
            color: #fff;
            cursor: pointer;
          }
        }
      }
    }
  }
  